// ...
let textOmit = (value, num) => {
  if (!value) return
  if (value.length > num) {
    return value.substring(0, num) + '...'
  }
  return value
}

// ()
let textCutOut = (value, num) => {
  if (!value) return
  if (value.length > num) {
    return value.substring(0, num)
  }
  return value
}

export {textOmit, textCutOut}



